  {
    Id: "44",
    IdZekr: "10",
    Name: `أحد تلك الأدعية بتم البدأ بها: - اللهم باعد بيني وبين خطاياي كما باعدت بين المشرق والمغرب، اللهم نقني من الخطايا كما ينقى الثوب الأبيض من الدنس، اللهم اغسل خطاياي بالماء والثلج والبَرَد - وجهت وجهي للذي فطر السماوات والأرض حنيفاً وما أنا من المشركين، إن صلاتي ونسكي ومحياي ومماتي لله رب العالمين، لا شريك له وبذلك أمرت وأنا من المسلمين. ، وفي رواية: وأنا أول المسلمين - سبحانك اللهم وبحمدك، وتبارك اسمك، وتعالى جدُّك، ولا إله غيرك`,
