const inc = (id, count, counter) => {
  const arr = azkar.value;

  arr.forEach((el, i) => {
    if (el.Id == id) {
      if (counter > 0) {
        arr[i].Counter--;
      } else {
        arr[i].Counter = count;
      }
    }
  });
};

const route = useRoute();
const azkarFiltered = ref([]);
onBeforeMount(() => {
  azkarFiltered.value = azkar.value.filter(
    (el) => el.IdZekr == route.params.id
  );
});
